import React, { useState, useEffect, useReducer } from "react";
import { useParams, useHistory } from "react-router-dom";
import {
  postRequest,
  getRequest,
  putRequest,
  deleteRequest,
} from "../../utils/apiRequrest";
import { BASE_URL, SAVE_CALL_ID, GET_CALL_ID } from "../../utils/apiEndpoints";
import CallPageLink from "../UI/CallPage/CallPageLink";
import CallPageFooter from "../UI/CallPage/CallPageFooter";
import Messenger from "../UI/CallPage/Messenger";
import MessageListReducer from "../../reducers/MessageListReducer";
import Peer from "simple-peer";
import io from 'socket.io-client'


import("./CallPage.scss");

const initialState = [];
let peer = null;
const socket = io.connect("http://localhost:4000")


function CallPage() {
  const history = useHistory();
  const { id } = useParams();
  const isAdmin = history.location.hash === "#init" ? true : false;
  const url = `${window.location.origin}${window.location.pathname}`;
  let alertTimeout = null;

  const [MessageList, setMessageListReducer] = useReducer(
    MessageListReducer,
    initialState
  );

  const [streamObj, setStreamObj] = useState();
  const [screenCastStream, setScreenCastStream] = useState();
  const [meetinginfoPopUp, setMeetinginfoPopUp] = useState(false);
  const [isPresenting, setIsPresenting] = useState(false);
  const [isMessenger, setIsMessenger] = useState(false);
  const [messageAlert, setMessageAlert] = useState({});
  const [isAudio, setIsAudio] = useState(false);

  useEffect(() => {
    if (isAdmin) {
      setMeetinginfoPopUp(true);
    }
       initWebRTC();
       socket.on("code", (data) => {
            peer.signal(data)
       })
  }, []);

     const getRecieverCode = async () => {
          const response = await getRequest(`${BASE_URL}${GET_CALL_ID}/${id}`);
        
          if (response.code) {
               peer.signal(response.code);
          }
     }
  const initWebRTC = () => {
    navigator.mediaDevices
      .getUserMedia({
        video: true,
        audio: true,
      })
      .then((stream) => {
        peer = new Peer({
          initiator: isAdmin,
          trickle: false,
          stream: stream,
        });
           
           if (!isAdmin) {
                getRecieverCode();
           }
           
        peer.on("signal", async (data) => {
          if (isAdmin) {
            let payload = {
              id,
              signalData: data,
            };
            await postRequest(`${BASE_URL}${SAVE_CALL_ID}`, payload);
          } else {
               socket.emit('code', data, (cbData) => {
                    console.log('code sent');
               })
          }
        });
        peer.on("connect", () => {
          console.log("Peer Connected ...");
        });

        peer.on("stream", (stream) => {
          let video = document.querySelector("video");
          if ("srcObject" in video) {
            video.srcObject = stream;
          } else {
            video.src = window.URL.createObjectURL(stream);
             }
             video.play();
        });
      })
         .catch((err) => {
            console.log(err)
       })
  };

  return (
    <div className="callpage-container">
      <div className="video-container">
        <video id="video" className="video" src={peer} controls />
      </div>
      {isAdmin && meetinginfoPopUp && (
        <CallPageLink
          url={url}
          setMeetinginfoPopUp={setMeetinginfoPopUp}
          meetinginfoPopUp={meetinginfoPopUp}
        />
      )}

      <CallPageFooter />
      <Messenger />
    </div>
  );
}

export default CallPage;
