import React, { useState, useRef } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlus,
  faUserPlus,
  faCopy,
  faShieldAlt,
  faWindowMaximize,
} from "@fortawesome/free-solid-svg-icons";
import("./CallPageLink.scss");

/**
 * Share Link Function
 * @param {url, setmeetinginfoPopUp as onClick, meetinginfoPopUp} param0
 * @returns
 */

function CallPageUi({ url, setMeetinginfoPopUp, meetinginfoPopUp }) {
  const [successMsg, setSuccessMsg] = useState(false);
  const linkRef = useRef(null);

  return (
    <div className="callpageui">
      <div className="shareLink">
        {/**
         * Heading and Close
         * */}
        <div className="heading">
          <div className="">Your meeting's ready</div>
          <FontAwesomeIcon
            className="icon icon-max"
            icon={!meetinginfoPopUp ? faWindowMaximize : faPlus}
            onClick={() => {
              setMeetinginfoPopUp(false);
            }}
          />
        </div>
        {/**
         * Share To Others
         * */}
        <div className="btn-group">
          <button className="btn btn-primary">
            <FontAwesomeIcon
              className="icon icon-user-plus"
              icon={faUserPlus}
            />
            Add Others
          </button>
        </div>
        <div className="meeting-msg">
          <p>
            Or Share this meetings link with others you want in the meetings
          </p>
        </div>
        {/**
         * Copy to Clipboard
         * */}
        <div className="meeting-links">
          <input className="link" ref={linkRef} disabled value={url} />
          <FontAwesomeIcon
            className={successMsg ? "icon icon-copy active" : "icon icon-copy"}
            icon={faCopy}
            onClick={() => {
              navigator.clipboard.writeText(url);
              setSuccessMsg(true);
            }}
          />
        </div>
        {/**
         * shild Message
         * */}
        <div className="shild-msg">
          <FontAwesomeIcon className="icon icon-shild" icon={faShieldAlt} />
          <span>
            People who use this meetings link must get your permission before
            they join.
          </span>
        </div>
        {/**
         * Join With Email
         * */}
        <div className="join-as">Joined as systembugbd@gmail.com</div>
        <span className="copiedSuccess">
          {successMsg
            ? "URL Copied to Clipboad Successfully!"
            : "Please Click to Copy Button to Copy the URL"}
        </span>
      </div>
    </div>
  );
}

export default CallPageUi;
